
"use client";

import { useEffect, useState } from "react"
import "./globals.css"

const handleClick = () => {
    const audio = new Audio('/sounds/minecraft_button_sound.mp3');
    audio.play();
}


export default function Login() {
     const [username, setUsername] = useState("");
     const [password, setPassword] = useState("");

     const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        // api
     }

    return <>
    <div className="main">
        <div className="container">
            <form onSubmit={handleLogin} className="form">
                <h2 className="title">Singleplayer</h2>
                <input
                    type="text"
                    placeholder="Username"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className="input"
                    required>
                </input>
                <input
                    type="password"
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="input"
                    required>
                </input>

                <div className="buttons">
                    <button type="submit" className="loginButton" onClick={handleClick}> Login</button>
                    <button
                        type="button"
                        onClick={() => {
                            handleClick();
                            setTimeout  (() => {
                                window.location.href = "/register";
                            }, 600);}}
                        className="createButton"
                    >
                        Create New Account
                    </button>
                </div>
                < a href="/imFatFuckMoronThatDontRememberHisAccount" className="recoverLink">
                    Forget Password?
                </a>

            </form>
        </div>

    </div>

    </>
}